body {
	margin: 0;
	font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
	background-color: darkgreen;
}

/*ANIMACIONES*/
.roll-in-left {
	-webkit-animation: roll-in-left 0.3s ease-out both;
	animation: roll-in-left 0.3s ease-out both;
}

@-webkit-keyframes roll-in-left {
	0% {
		-webkit-transform: translateX(-800px) rotate(-540deg);
		transform: translateX(-800px) rotate(-540deg);
		opacity: 1;
	}
	100% {
		-webkit-transform: translateX(0) rotate(0deg);
		transform: translateX(0) rotate(0deg);
		opacity: 1;
	}
}
@keyframes roll-in-left {
	0% {
		-webkit-transform: translateX(-800px) rotate(-540deg);
		transform: translateX(-800px) rotate(-540deg);
		opacity: 1;
	}
	100% {
		-webkit-transform: translateX(0) rotate(0deg);
		transform: translateX(0) rotate(0deg);
		opacity: 1;
	}
}

.mesa {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	margin-top: 10px;
}

.mesa h1 {
	font-size: 4rem;
	margin-bottom: 0;
	color: white;
}

.mesa button {
	border: none;
	padding: 15px;
	font-size: 1.3rem;
	background-color: black;
	color: white;
	border-radius: 3px;
	transition: all 0.2s ease-in-out;
}

.mesa button:hover {
	cursor: pointer;
}

.mesa .cartas .card-div {
	position: absolute;
	width: 225px;
	height: 314px;
}
.mesa .cartas {
	margin-top: 70px;
	display: flex;
	justify-content: center;
}

.mesa .cartas img {
	position: absolute;
}

@media only screen and (max-width: 768px) {
	.mesa h1 {
		font-size: 3rem;
		margin-bottom: 0;
	}

	.mesa .cartas .img {
		margin: auto;
		width: fit-content;
	}
}
